<template lang="pug">
    div
        h1 {{message}}
        button(@click="init") 再戦
        input(type="number" v-model="N")
        table
            tr(v-for="i, k in field")
                td(v-for="j, l in i" @click="onMouseDown(k, l)")
                    mark-a(:mark="j")
</template>

<script>
    import MarkA from "./mark";

    export default {
        name: 'Tapitapi',
        props: {},
        components: {
            MarkA
        },

        data: () => ({
            field: undefined,

            turn: 1,
            message: "",
            user_name: {1: '水色', 2: 'オレンジ'},
            N: 4,
        }),

        methods: {
            init() {
                this.N = parseInt(this.N)
                this.field = []
                this.message = ''
                this.fin = false
                for (let _ of Array(this.N)) {
                    _
                    this.field.splice(0, 0, [...Array(this.N).fill(0)])
                }
                this.field = JSON.parse(JSON.stringify(this.field))
            },
            onMouseDown(i, j) {
                if (this.fin || this.field[i][j] === 3) {
                    return
                }

                if (this.field[i][j] && this.field[i][j] < 3) {
                    this.field[i][j] = 3
                    this.field = JSON.parse(JSON.stringify(this.field))
                    this.turn = this.turn % 2 + 1
                    return
                }

                this.field[i][j] = this.turn

                this.field = JSON.parse(JSON.stringify(this.field))
                this.turn = this.turn % 2 + 1

                this.judge()
            },

            judge() {
                for (let i in Array.from({length: this.N}, (v, k) => k)) {
                    let f = !!this.field[i][0]
                    for (let j in Array.from({length: this.N-1}, (v, k) => k)) {
                        f = f && this.field[i][parseInt(j)] === this.field[i][parseInt(j)+1]
                    }
                    if (f) {
                        this.message = `${this.user_name[this.field[i][0]]}の勝ち`
                        this.fin = true
                        return
                    }

                    f = !!this.field[0][i]
                    for (let j in Array.from({length: this.N-1}, (v, k) => k)) {
                        f = f && this.field[parseInt(j)][i] === this.field[parseInt(j)+1][i]
                    }
                    if (f) {
                        this.message = `${this.user_name[this.field[0][i]]}の勝ち`
                        this.fin = true
                        return
                    }
                }


                let f = !!this.field[0][0]
                for (let i in Array.from({length: this.N-1}, (v, k) => k)) {
                    f = f && this.field[parseInt(i)][parseInt(i)] === this.field[parseInt(i)+1][parseInt(i)+1]
                }
                if (f) {
                    this.message = `${this.user_name[this.field[0][0]]}の勝ち`
                    this.fin = true
                    return
                }

                f = !!this.field[0][this.N-1]
                for (let i in Array.from({length: this.N-1}, (v, k) => k)) {
                    // eslint-disable-next-line no-console
                    console.log(parseInt(i), this.N-parseInt(i)-1, this.N-(parseInt(i)+2))
                    f = f && this.field[parseInt(i)][this.N-parseInt(i)-1] === this.field[parseInt(i)+1][this.N-(parseInt(i)+2)]
                }
                if (f) {
                    this.message = `${this.user_name[this.field[0][this.N-1]]}の勝ち`
                    this.fin = true
                    return
                }

            }
        },

        watch: {
            N(n, o) {
                n
                o
                this.init()
            }
        },

        created() {
            this.init()
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    td {
        width: 200px;
        height: 200px;
    }

    h1 {
        color: orangered;
    }
</style>
